@import "../Content_Colours.scss";
@import "../../../../fonts/fonts.scss";
@import "../Content_Colours.scss";

@mixin contentFont {
  font-family: "DrukWide";
  font-size: 1.9rem;
  text-align: justify;
}
@mixin monumentExtendedFont {
  font-family: "MonumentExtended";
  font-size: 1.2rem;
  text-align: justify;
}

/* The container must be positioned relative: */
.custom-select {
  width: 100%;
  position: relative;
  border: 0;
  border-bottom: 1px solid $dark-gray-text;
  margin-bottom: 2.5rem;
}

.custom-select select {
  display: none; /*hide original SELECT element: */
}

.select-selected {
  @include contentFont();
  color: black;
  background-color: white;
}

